*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  scroll-behavior: smooth;
}

body{
  width: 100%;
  overflow-x: hidden;
}
/* *************************************navigation bar******************************************************************** */

nav{
  width: 100%;
  padding: 20px 40px;
  background-color: black;
  color: white;
  border: none;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
nav .logo{
  text-decoration: none;
  font-size: 35px;
  font-weight: 700;
  color: orange;
  transition: all .4s ease-out;
}
nav .logo:hover{
  color: white;
  text-shadow: 0 0 20px orange;
  transform: translateY(-10px);
}
nav .navlist ul{
  display: flex;
  align-items: center;
  justify-content: center;
}
nav .navlist li{
  list-style: none;
  margin-left: 20px;
}
nav .navlist .navitem{
  color: white;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}
nav .navlist .navitem:hover{
  color: orange;
}
nav .search{
  border: 2px solid rgb(66, 183, 230);
  padding: 9px 14px;
  color: black;
  background-color: gainsboro;
  border-radius: 30px;
  font-size: 14px;
  transition: all .4s;
}
nav .search:focus{
  outline: none;
  border: 2px solid orange;
  background-color: black;
  color: white;
}

/* *************************************navigation bar******************************************************************** */
section{
  padding: 30px 0;
  width: 100%;
  min-height: 80vh;
}

/* *************************************foooter page**************************************************************** */

.footer{
  width: 100%;
  border: 1px solid black;
}

