@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');


*{
    font-family: "Roboto", sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
.containe{
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
    background-repeat: no-repeat;
    min-height: 100vh;
    width: 100vw;
} 
.ftd{
    background-color: transparent;
    border: none;
    color: white;
}
.error{
    font-size: 25px;
    font-weight: 400;
    color: blue;
}

.pages{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image:linear-gradient( rgba(0, 0, 0, 0.8),rgba(0, 0, 0, 0.8)) ,url('../asset/bg1.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.pagess{
    background-color: rgb(56, 51, 51);
    animation: bgcol 4s infinite;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.page2 p{
    color: white;
    font-size: 20px;
    letter-spacing: 1px;
    font-weight: 400;
}

.page2 span{
    color: white;
    background-color: blueviolet;
    animation: bgcol 4s infinite;
    padding: 10px 20px;
    border-radius: 20px;
    cursor: pointer;
}
@keyframes bgcol {
    0%{
        background-color: rgb(230, 16, 116);
    }
    50%{
        background-color: rgb(2, 2, 109);
    }
    90%{
        background-color: rgb(161, 9, 231);
    }
}
.page2 h6{
    font-size: 20px;
    color: whitesmoke;
}